body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: white;
  color: #000;
}

.wrapper {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background-color: white;
  padding: 20px;
}

.App {
  max-width: 1000px;
  width: 100%;
  animation: fadeIn 1s ease;
}

input, button {
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
  outline: none;
}

input {
  padding: 10px;
  width: 280px;
  background-color: #fff;
  color: #000;
  margin-right: 12px;
}

button {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  padding: 10px 24px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #cc0000;
}

.channel-info {
  margin-top: 30px;
}

.videos-list {
  margin-top: 40px;
}

.video-item {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
